import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { map } from 'rxjs/operators';
import { BehaviorSubject, Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { User } from '../models/user';

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  private currentUserSubject: BehaviorSubject<User>;
  public currentUser: Observable<User>;

  get isAuthenticated(): boolean {
    return !!this.currentUserSubject.value;
  }

  constructor(private router: Router, private http: HttpClient) {
    this.currentUserSubject = new BehaviorSubject<User>(
      JSON.parse(localStorage.getItem('currentUser'))
    );
    this.currentUser = this.currentUserSubject.asObservable();
  }

  isTokenExpired(): boolean {
    if (this.isAuthenticated) {
      const idToken = this.currentUserValue.token;
      if (idToken !== null) {
        return true; // this.jwtHelperService.isTokenExpired(idToken, expirationTokenOffsetSeconds);
      }
    }
    return false;
  }

  public get currentUserValue(): User {
    return this.currentUserSubject.value;
  }

  login(email: string, password: string) {
    return this.http
      .post<any>(`${environment.apiUrl}/users/authenticate`, {
        email,
        password,
      })
      .pipe(
        map((user: User) => {
          // login successful if there's a jwt token in the response
          if (user && user.token && user.ProfilComplete) {
            // store user details and jwt token in local storage to keep user logged in between page refreshes
            localStorage.setItem('currentUser', JSON.stringify(user));
            this.currentUserSubject.next(user);
          }

          return user;
        })
      );
  }

  setUserProfil(user: User) {
    return this.http
      .post<any>(`${environment.apiUrl}/users/set-user-profil`, user)
      .pipe(
        map((user: User) => {
          // login successful if there's a jwt token in the response
          if (user && user.token && user.ProfilComplete) {
            // store user details and jwt token in local storage to keep user logged in between page refreshes
            localStorage.setItem('currentUser', JSON.stringify(user));
            this.currentUserSubject.next(user);
          }

          return user;
        })
      );
  }

  signup(email: string, password: string, confirmedPassword: string) {
    return this.http
      .post<any>(`${environment.apiUrl}/users/signup`, {
        email,
        password,
        confirmedPassword,
      })
      .pipe(
        map((user) => {
          // signup successful
          return user;
        })
      );
  }

  emailAlready(email: string) {
    return this.http
      .post<any>(`${environment.apiUrl}/users/email-already`, { email })
      .pipe(
        map((already) => {
          return already;
        })
      );
  }

  async changePassword(email: string, recoveryCode: string) {
    try {
      // Send request
      console.log(email, recoveryCode);

      return {
        isOk: true,
      };
    } catch {
      return {
        isOk: false,
        message: 'Failed to change password',
      };
    }
  }

  async resetPassword(email: string) {
    try {
      // Send request
      console.log(email);

      return {
        isOk: true,
      };
    } catch {
      return {
        isOk: false,
        message: 'Failed to reset password',
      };
    }
  }

  logOut() {
    localStorage.removeItem('currentUser');
    this.currentUserSubject.next(null);
    this.router.navigate(['/login']);
  }
}
