import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpResponse,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HTTP_INTERCEPTORS,
} from '@angular/common/http';
import { Observable, of, throwError } from 'rxjs';
import { delay, mergeMap, materialize, dematerialize } from 'rxjs/operators';
import { Role, User } from '../models/user';

const users: User[] = [
  {
    id: 1,
    token: null,
    username: 'admin',
    password: 'admin',
    email: 'admin@rh-projet.com',
    FirstName: 'ADMIN',
    LastName: 'Demo',
    ProfilComplete: true,
    role: Role.Admin,
  },
  {
    id: 2,
    token: null,
    username: 'user',
    password: 'user',
    email: 'user@rh-projet.com',
    FirstName: 'NORMAL',
    LastName: 'Demo',
    ProfilComplete: true,
    role: Role.User,
  },
];

@Injectable()
export class FakeBackendInterceptor implements HttpInterceptor {
  intercept(
    request: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    const { url, method, headers, body } = request;

    // wrap in delayed observable to simulate server api call
    return of(null)
      .pipe(mergeMap(handleRoute))
      .pipe(materialize()) // call materialize and dematerialize to ensure delay even if an error is thrown (https://github.com/Reactive-Extensions/RxJS/issues/648)
      .pipe(delay(500))
      .pipe(dematerialize());

    function handleRoute() {
      switch (true) {
        case url.endsWith('/users/authenticate') && method === 'POST':
          return authenticate();
        case url.endsWith('/users/signup') && method === 'POST':
          return createAccount();
        case url.endsWith('/users/set-user-profil') && method === 'POST':
          return setUserProfil();
        case url.endsWith('/users/email-already') && method === 'POST':
          return emailAlready();
        case url.endsWith('/users') && method === 'GET':
          return getUsers();
        case url.match(/\/users\/\d+$/) && method === 'GET':
          return getUserById();
        default:
          // pass through any requests not handled above
          return next.handle(request);
      }
    }

    // route functions
    function authenticate() {
      const { email, password } = body;
      const user = users.find(
        (x) => x.email === email && x.password === password
      );
      if (!user) return error('Email or password is incorrect');
      let u = { ...user };
      u.token = `fake-jwt-token.${user.id}`;
      return ok(u);
    }

    function createAccount() {
      const { email, password } = body;
      let user: User = {
        id: Math.max(...users.map((u) => u.id)) + 1,
        token: null,
        username: null,
        password: password,
        email: email,
        FirstName: null,
        LastName: null,
        ProfilComplete: false,
        role: Role.User,
      };
      users.push(user);

      return ok(user);
    }

    function setUserProfil() {
      const { email } = body;
      let user: User = users.find((x) => x.email === email);
      if (!user) return error('User not exist');
      user.FirstName = body.FirstName;
      user.LastName = body.LastName;
      user.Position = body.Position;
      user.ProfilComplete = body.ProfilComplete;
      user.entreprise = body.entreprise;

      let u: User = { ...user };
      u.token = `fake-jwt-token.${user.id}`;
      return ok(u);
    }

    function emailAlready() {
      const { email } = body;
      const user = users.find((x) => x.email === email);
      if (user) return ok(true);
      else return ok(false);
    }

    function getUsers() {
      if (!isAdmin()) return unauthorized();
      return ok(users);
    }

    function getUserById() {
      if (!isLoggedIn()) return unauthorized();

      // only admins can access other user records
      if (!isAdmin() && currentUser().id !== idFromUrl()) return unauthorized();

      const user = users.find((x) => x.id === idFromUrl());
      return ok(user);
    }

    // helper functions
    function ok(body) {
      return of(new HttpResponse({ status: 200, body }));
    }

    function unauthorized() {
      return throwError({ status: 401, error: { message: 'unauthorized' } });
    }

    function error(message) {
      return throwError({ status: 400, error: { message } });
    }

    function isLoggedIn() {
      const authHeader = headers.get('Authorization') || '';
      return authHeader.startsWith('Bearer fake-jwt-token');
    }

    function isAdmin() {
      return isLoggedIn() && currentUser().role === Role.Admin;
    }

    function currentUser() {
      if (!isLoggedIn()) return;
      const id = parseInt(headers.get('Authorization').split('.')[1]);
      return users.find((x) => x.id === id);
    }

    function idFromUrl() {
      const urlParts = url.split('/');
      return parseInt(urlParts[urlParts.length - 1]);
    }
  }
}
